.board {
  //min-width: 442px;// TODO: refactor this
  background: transparent;// #111;
  padding: 10px;
  border: 1px solid #555;

  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;

  &__row{
    z-index: 0;
    position: relative;
    font-size: 1em;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: center;
    align-items: center;
  }

}

.board {
  transform: perspective(700px) rotateX(0) rotateY(0) rotateZ(0);
  transform-style: preserve-3d;
  transition: transform 1s, font-size 0.1s;
  &--rotated {
    transform: perspective(700px) rotateX(60deg) rotateY(0deg) rotateZ(45deg);
  }
}
