@charset "UTF-8";
.game {
  display: flex;
  flex-direction: row;
  align-items: flex-start; }
  .game > * {
    margin: 5px; }
  .game__board {
    border: 1px solid #666; }
  .game__list {
    float: left;
    clear: both; }
  .game .button, .game .actions button, .game .auto-cycle {
    color: #999;
    background: #444;
    margin-top: 5px;
    margin-right: 5px;
    border: 2px solid #666;
    padding: 5px; }
    .game .button.next_tick, .game .actions button.next_tick, .game .auto-cycle.next_tick {
      border: 2px solid green; }
    .game .button.execute_actions, .game .actions button.execute_actions, .game .auto-cycle.execute_actions {
      border: 2px solid #1378b3; }
    .game .button.nuke, .game .actions button.nuke, .game .auto-cycle.nuke {
      color: #e00; }
    .game .button:active, .game .actions button:active, .game .auto-cycle:active {
      border-color: white; }
  .game .game__info {
    border: 1px solid #666;
    padding: 10px; }
    .game .game__info .actions {
      padding: 0 0 10px 0;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      align-content: space-between;
      justify-content: flex-start;
      flex-wrap: wrap; }
      .game .game__info .actions .action_row {
        border: 0px solid red;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-content: center;
        justify-content: space-evenly;
        align-items: center; }
        .game .game__info .actions .action_row > * {
          flex: 1;
          min-width: 150px;
          box-sizing: border-box; }
      .game .game__info .actions .auto-cycle {
        white-space: pre;
        position: relative;
        color: #666; }
        .game .game__info .actions .auto-cycle span {
          padding-left: 15px; }
        .game .game__info .actions .auto-cycle input {
          display: none;
          border: 1px solid #666;
          background: #222; }
          .game .game__info .actions .auto-cycle input:checked ~ span {
            color: white;
            position: relative; }
            .game .game__info .actions .auto-cycle input:checked ~ span::before {
              position: absolute;
              left: 1px;
              top: -1px;
              content: "✓"; }
    .game .game__info .interaction-container {
      display: grid;
      font-family: Verdana;
      grid-template-columns: 50% 50%;
      margin-bottom: 10px; }
      .game .game__info .interaction-container .selected,
      .game .game__info .interaction-container .targeted {
        display: block;
        border: 1px solid #746200; }
        .game .game__info .interaction-container .selected__label,
        .game .game__info .interaction-container .targeted__label {
          display: block;
          text-align: center;
          padding: 5px; }
      .game .game__info .interaction-container .selected {
        border-color: #095c09;
        margin-right: 10px; }
        .game .game__info .interaction-container .selected__label {
          background: #095c09;
          border: 1px solid #13af13; }
      .game .game__info .interaction-container .targeted {
        border-color: #ac7000; }
        .game .game__info .interaction-container .targeted__label {
          background: #ac7000;
          border: 1px solid #d88c00;
          color: black; }
      .game .game__info .interaction-container .targeted ul {
        list-style: none;
        padding: 0;
        margin: 0;
        margin-top: 3px; }

.step-counter {
  background: black;
  position: relative;
  margin-right: 5px;
  margin-top: 6px;
  padding: 5px;
  border: 2px solid #456; }

.status {
  margin-bottom: 10px; }

.kbd-navigation .square:focus {
  background: #ddd; }
